export default {
	computed: {
		...Vuex.mapGetters({
			selected: 'multicart/selected',
			items: 'multicart/notSelected'
		}),
		canToggle() { return !!this.items.length; }
	},
	methods: {
		select({ CODE })
		{
			this.$store.dispatch('multicart/select', CODE)
				.then(() => {
					return this.$store.dispatch('cart/fetch');
				});
		}
	}
}
