import QuickBasketEmpty from './quick-basket-empty.vue';

export default {
	components: { QuickBasketEmpty },
	props: {
		isShowed: {
			type: Boolean,
			default: false
		},
		pathToBasket: {
			type: String,
			default: ''
		},
		pathToOrder: {
			type: String,
			default: ''
		},
		signedParameters: {
			type: String,
			default: ''
		}
	},
	data()
	{
		return {
			loaded: false,
			needRefresh: false,
			categories: {},
		}
	},
	created()
	{
		this.load();
		this.unsubscribe = this.$store.subscribeAction((action, state) => {
			if (action.type !== 'cart/fetch')
				return

			if (this.isShowed)
			{
				this.refresh();
			}
			else
			{
				this.needRefresh = true;
			}
		})
	},
	beforeDestroy()
	{
		this.unsubscribe();
	},
	computed: {
		messages()
		{
			return Object.freeze({
				'RS_B2B_SBBL_TO_ORDER': BX.message('RS_B2B_SBBL_TO_ORDER'),
				'RS_B2B_SBBL_TO_BASKET': BX.message('RS_B2B_SBBL_TO_BASKET'),
			});
		},
		readyProducts()
		{
			return this.categories.ready || [];
		},
	},
	methods: {
		async load()
		{
			try
			{
				const result = await new Promise((resolve, reject) => {
					const signedParameters = this.signedParameters
					const data = { c: 'sale.basket.basket.line' };
					BX.ajax.runAction('redsign:b2bportal.api.cart.getItems', { signedParameters, data })
						.then(resolve, reject)
				});

				this.loaded = true;
				this.categories = result.data;

				return result;
			}
			catch(e)
			{
				console.error(e);
			}

			return false;
		},

		refresh()
		{
			return this.load()
				.then(res => {
					this.$emit('refreshed');
					return res;
				});
		},

	},
	watch: {
		isShowed(val)
		{
			if (val && this.needRefresh)
				this.refresh();
		}
	}
}
